.card {
    width: 10rem;
    height: 94px;
    border: 4px solid #8b3a87;
    border-radius: 16px;
    margin: 11px 8px;
    padding: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.card h6 {
    font-size: small;
    margin: 0px;
}

.card p {
    font-size: small;
    margin: 0px;
}

.link {
    border-radius: 10px;
    margin-top: 1.5rem;
    padding: 6px;
    background: #8b3a87;
    color: #000;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
}