html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
  height: 100vh;
}
article,
aside,
footer,
header,
nav,
section {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
figcaption,
figure,
main {
  display: block;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace, monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}
b,
strong {
  font-weight: inherit;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
dfn {
  font-style: italic;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
audio,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
img {
  border-style: none;
  max-width: 100%;
}
svg:not(:root) {
  overflow: hidden;
}
button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  --webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0; /* 3 */
  white-space: normal;
}
progress {
  display: inline-block;
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  --webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  --webkit-appearance: none;
}
::-webkit-file-upload-button {
  --webkit-appearance: button;
  font: inherit;
}
details,
menu {
  display: block;
}
summary {
  display: list-item;
}
canvas {
  display: inline-block;
}
template {
  display: none;
}
[hidden] {
  display: none;
}

.card {
    width: 10rem;
    height: 94px;
    border: 4px solid #8b3a87;
    border-radius: 16px;
    margin: 11px 8px;
    padding: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.card h6 {
    font-size: small;
    margin: 0px;
}

.card p {
    font-size: small;
    margin: 0px;
}

.link {
    border-radius: 10px;
    margin-top: 1.5rem;
    padding: 6px;
    background: #8b3a87;
    color: #000;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
}
.home {
  height: 100vh;
  overflow: hidden;
}

.header {
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100;
  background: #8b3a87;
  height: 50px;
}

.logo {
  width: 35px;
  height: 35px;
  margin: 5px 0 0 1rem;
  overflow-y: auto;
}

.main {
  flex-grow: 1;
  position: absolute;
  top: 50px;
  bottom: 56px;
  left: 0px;
  right: 0px;
  overflow: hidden;
}

.main-header {
  text-align: center;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.2);
  height: 86px;
  position: absolute;
  top: 1px;
  left: 0px;
  right: 0px;
  padding-bottom: 5px;
}

.main-header span {
  text-align: center;
}

.main-header h1 {
  font-size: 2em;
  margin: 0.2em 0;
}

.main-container {
  height: calc(100% - 100px);
  overflow: scroll;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -5px;
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 4px;
}

.footer {
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  z-index: 100;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background: #67c1ee;
  height: 56px;
  font-size: small;
}

.footer-item {
  text-align: center;
}

.footer-item p {
  margin: 1px;
  text-align: center;
}

.button {
  display: block;
  width: 115px;
  height: 25px;
  background: #4E9CAF;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  color: white;
  font-weight: bold;
  line-height: 25px;
}

@media screen and (min-width: 1280px) { 

  .main-container {
    grid-gap: 1vh 6vw;
    gap: 1vh 6vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: 0 0px;
    overflow: unset;
    padding-left: 200px;
    padding-right: 100px;
    height: calc(100% - 100px);
    margin: 0 -5px;
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 4px;
  }

}

@media screen and (max-width: 712px) { 

  .footer {
    font-size: x-small;
  }

}


